import React from 'react';
import { Link } from 'react-router-dom';

const SpotsList = ({ spots }) => (
				<>
				<p>{spots.title}</p>
				{spots.map((spot, key) =>(
					<Link className="article-list-item" key={key} to={`/spot/${spot.name}`}>
					<h3>{spot.title}</h3>							
					<p>{spot.content[0].substring(0,150)}...</p>
					</Link>
				))}
				</>
							
							

);

export default SpotsList;