import React from 'react';
import { Link } from 'react-router-dom';


const HomePage = () => (
	<>
	<section id="two" className="wrapper style2">
				<div className="inner">
					<div className="box">
						<div className="content">
							<header className="align-center">
								<p>Bienvenido a WEEDSPOT</p>
								<h2>"420 Friendly"</h2>
							</header>
							<p>Gracias por visitar WeedSpot, aquí puedes encontrar desde el mejor hasta el "ni loc@ te acerques" en lugares/spots para encender un porrito.
							Creado por la propia comunidad de la hierva aquí puedes encontrar toda la información sobre la "MOTA" y sus actividades. Si eres turista o local este nuevo concepto te encantará; conoce y visita los mejores/peores lugares para fumar y pasar un rato relajado (o no) mientras disfrutas del spot/POD. 
							Alertas, calificaciones, opiniones y todo lo que tenga que ver con la cultura de la marihuana.
							Ten acceso a estos y mas servicios (Se aplican restricciones de acuerdo con las leyes sobre DROGAS de su pais/estado).
							Recomendaciones basandose en su ubicación y/o "Grado Stoner".
							Integración con google, FB, Twitter o simplemente ingresá como anónimo para tu protección y la de otros en la comunidad.
							Ayudanos a crecer!! Envianos tus fotos via nuestro canal de Telegram con una descripcion y posible nombre del lugar, puede ser la calle o apodo conocido de los alrededores. </p>

							<p>Contacténos si quieres ser parte de este proyecto, la idea principal es crecer y lanzar una aplicación en conjunto con el sitio. Diseño y nombre del sitio pueden variar segun desarrollo.
							Para mas info visita/comparte: <Link to="/Telegram">WEESPOT @Telegram Channel</Link></p>

							<p>Respeto para tod@s.
							Respetamos los estandares y las leyes/regulaciones basándonos en su ubiciación(país/estado). El contenido en el sitio web y la aplicación puede variar dependiendo de las regulaciones en su localidad. </p>
						</div>
					</div>
				</div>
			</section>
	</>
);
export default HomePage;