import React, {Component} from 'react';
import {BrowserRouter as Router, Route, Switch} from 'react-router-dom';
import HomePage from './pages/HomePage';
import SpotsListPage from './pages/SpotsListPage';
import SpotPage from './pages/SpotPage';
import AboutPage from './pages/AboutPage';
import NotFoundPage from './pages/NotFoundPage';
import NavBar from './NavBar';

import './App.css';

class App extends Component{
	render(){
  return (
	<Router>
	 <div className="content">

	 <NavBar />
			<Switch>
			<Route path="/" component={HomePage} exact/>
		  <Route path="/about" component={AboutPage}/>
		  <Route path="/spot/:name" component={SpotPage}/>
		  <Route path="/spots-list" component={SpotsListPage}/>
		 <Route path="/Telegram" component={() => {window.location.href = 'https://t.me/WEESpot'; return null;}}/>
		 <Route component={NotFoundPage}/>
			
			</Switch>
		  
	

	 <script src="./js/jquery.min.js"></script>	  
			<script src="./js/jquery.scrollex.min.js"></script>
			<script src="./js/skel.min.js"></script>
			<script src="./js/util.js"></script>
			<script src="./js/main.js"></script>
    </div>
	
	</Router>
	
   
		);
	}
}

export default App;
