import React from 'react';

const LikesSection = ({ spotName, likes, setSpotInfo }) => {
	const likeSpot = async () => {
		
		const result = await fetch(`/api/spots/${spotName}/likes`, {
			method: 'post',
		});
		const body = await result.json();
		setSpotInfo(body);
	}
	
	return (
	<div id="upvotes-section">
		<button onClick={() => likeSpot()}>Me Gusta </button>
		<p>Este SPOT ha sido votado {likes} veces</p>
	</div>

	);
}
export default LikesSection;
